<template>
<div>
    <div class="pagebar-title"></div>

    <div class="module">
      <div class="mtitle"></div>
      <el-input v-model="form.name" placeholder=""></el-input>
    </div>

    <div class="module">
      <div class="mtitle"></div>
    <el-radio-group v-model="form.type">
      <el-radio-button label="1"></el-radio-button>
      <el-radio-button label="2"></el-radio-button>
      <el-radio-button label="3"></el-radio-button>
      <el-radio-button label="4"></el-radio-button>
      <el-radio-button label="5"></el-radio-button>
      <el-radio-button label="6"></el-radio-button>
      <el-radio-button label="7"></el-radio-button>
    </el-radio-group>
    </div>

    <div class="module">
      <div class="mtitle"></div>
      <el-input v-model="form.url" placeholder=""></el-input>
    </div>

    <div class="module">
      <div class="mtitle">Json</div>
      <!-- <el-input v-model="form.json" placeholder="Json"></el-input> -->
      <el-input
        type="textarea"
        placeholder=""
        v-model="form.putjison">
      </el-input>
    </div>

    <div class="module">
      <div class="mtitle"></div>
        <editor
          v-model="form.doc"
          :content="form.doc"
          class="editor"
        />
    </div>

    <div class="module">
      <div class="mtitle">Json</div>
      <!-- <el-input v-model="form.json" placeholder="Json"></el-input> -->
      <el-input
        type="textarea"
        placeholder=""
        v-model="form.outjson">
      </el-input>
    </div>

    <div class="module">
      <div class="mtitle"></div>
      <el-button @click="push"></el-button>
    </div>

</div>
</template>

<script>
import { Addandedit, Getdata } from "@/api/opendosc";
import Editor from "@/components/Editor.vue";
export default {
  name: 'Page404',
  data() {
    return {
        form: {
            id:null,
            type:null,
            name:null,
            url:null,
            doc:null,
            putjison:null,
            outjson:null
        }
    }
  },
  components:{
    Editor
  },
  methods:{
    push() {
      this.$http(Addandedit(this.form), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    getDocs(id){
      this.$http(Getdata({
        id:id
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
          let data = res.data
          this.form.id = data.id
          this.form.type = data.type
          this.form.name = data.name
          this.form.url = data.url
          this.form.doc = data.doc
          this.form.putjison = data.putjison
          this.form.outjson = data.outjson
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    }
  },
  created() {
    let id = this.$route.query.docid
    if (id){
      this.form.id = id;
      this.getDocs(id)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>
